import React, { useState, useEffect } from 'react';
import classes from './LoaderDesk.module.css'
import Header from '../components/Header/Header';
import ContentWithLoading from '../components/Content/Content';

const LoaderDesk = (props) => {
    const [loading, setloading] = useState(true);

    useEffect(() => {
        const interval = setInterval(() => {
            setloading(loading => false);
        }, 5000);
        return () => clearInterval(interval);
    }, []);

    return (
        <div>
            <div className={[classes.Desk, "text-uppercase font-weight-bolder"].join(' ')}>
                <Header><h1>Loader Desk</h1></Header>
            </div>
            <div className={classes.Card}>
                <ContentWithLoading isLoading={loading} />
            </div>
        </div>
    );
};

export default LoaderDesk;