import React from 'react';
import classes from './Content.module.css';
import WithLoading from '../../hoc/WithLoading '

const Content = (props) => {
    return (
        <div className={[classes.Paragraph, "d-flex align-items-center"].join(' ')}>
            <strong>Data Loaded Successfully......</strong>
        </div>
    );
};

const ContentWithLoading = WithLoading(Content)

export default WithLoading(ContentWithLoading);